































import Vue from 'vue'

export type DataType = {
  sum_money : number,
  strict_money : number
}
export default Vue.extend({
  name: 'Home',
  data(): DataType {
    return {
      sum_money : 500000,
      strict_money : 0
    }
  },
  computed: {
    current_percent() : number {
      return Math.ceil(this.sum_money * 100/ 1030000)
    }
  },
  created() {
    this.strict_money = this.sum_money
  },
  methods: {
    scrictParcent() : void {
      this.sum_money = this.strict_money
    }
  }
})
